/*loader*/


#loader-wrapper-world {
  visibility: hidden;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #0b0b0bdf;
    display: grid;
    place-items: center;

  }
  #loader-wrappr-world.active {
    visibility: visible;
  }
  #loader-wrapper-world.hidden {
    visibility: hidden;
  }
  .loader-world {
    border: 16px solid #e3e3e3;
    border-top: 16px solid #3498db;
    border-radius: 50%;
    width: 140px;
    height: 140px;
    animation: spin 1s linear infinite;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }
