@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@900&display=swap');


.home-body {
  /* background-image: url("/public/home-background.jpg"
    background-size: cover;
    background-position:center;*/
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
}
a {
  color: #ff1f8f;
}
.home-button {
  margin-top: 1em;
  margin-bottom: 1em;
  font-size: 2rem;
  text-align: center;
  background-color: transparent;
  color: #fff;
  padding: 8px 30px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

/*loader*/


#loader-wrapper {
visibility: visible;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #0b0b0b;
  display: grid;
  place-items: center;
  transition: visibility 1s, opacity 1s;

}
#loader-wrapper.hidden {
  opacity: 0;
  visibility: hidden;
}
.loader {
  border: 16px solid #e3e3e3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 140px;
  height: 140px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.language-button {
  height: 1em;
  font-size: 1rem;
  text-align: center;
  background-color: transparent;
  color: #fff;
  padding: 8px 11px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
  margin-top: 1em;
  margin-right: 1em;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}


.home-logos {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  position: absolute;
  width: 99vw;
  bottom: 1vh;
  height: 2em;
  gap: 30px;
}
.space{
