}
.innovlab{
margin-bottom: 0.1em;}
.icona {
  font-size: 1.7rem;
  color: red;
margin-right: 0.7em;
  cursor: pointer;
}

.name-modal {
  font-size: 1.2rem;
  margin-right: 0.4em;
}
.OAA {
}
.logos-background {
  width: 5em;
}
video {
  object-fit: cover;
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: -1;
}
.box-readmore{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90vw;
  max-height: calc(100vh - 200px);
  overflow-y: auto;
background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  color: white;
  border: 2px solid #000;
  box-shadow: 24;
padding: 1em;
  height: 90vh;
  text-align: justify;
}
.size{
  width: 79vw;
}
.box-verticalpanel{
  display: flex;
  flex-direction: row;
}
.home-button:hover {
  transition: all 0.3s ease-out;
  background: #fff;
  color: #242424;
  transition: 250ms;
}
.language-button:hover {
  transition: all 0.3s ease-out;
  background: #fff;
  color: #242424;
  transition: 250ms;
}
.home-button-section {
  width: 50vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.close{
  position: absolute;
  left: 90vw;
}

.home-name-section {
  width: 50vw;
  height: 100vh;
  display: flex;
  align-items: center;
 justify-content: center;
  flex-direction: column;
  color: white;
}
.home-button-background {
  background-color: rgba(0, 0, 0, 0.682);
  padding: 1em;
  border-radius: 5px;

}
.language-bar {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 100vw;
  position: absolute;
  height: 3em;
  top: 0vh;
}

.nad{
  white-space: nowrap;
  position: relative;
  left: 1em;
  font-family: 'Raleway', sans-serif;
  font-size:4rem;
  text-align: center;
	letter-spacing:0.1em;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: white;
  text-shadow:
						4px 4px #ff1f8f,
						10px 10px #000000;
}

.pod{
  font-size:2rem;
  position: relative;
  left: 1.6em;
  text-align: center;
  font-family: 'Raleway', sans-serif;
	letter-spacing:0.1em;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
  text-shadow:
						4px 4px #0065e9,
						7px 7px #000000;
}
